import { useState } from 'react';
import css from 'components/ContactForm/ContactForm.module.css';
import { nanoid } from 'nanoid';
import { useDispatch, useSelector } from 'react-redux';
import { selectContacts } from 'redux/contacts/selectors';
import { addContact } from 'redux/contacts/operation';
import toast, { Toaster } from 'react-hot-toast';

export const ContactForm = () => {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');

  const contacts = useSelector(selectContacts);
  const dispatch = useDispatch();

  let nameId = nanoid();
  let numberId = nanoid();

  const handleChange = event => {
    //   –ø—Ä–∏ –≤–≤–µ–¥–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –≤ –∏–Ω–ø—É—Ç –∑–∞–ø–∏—Å—ã–≤–∞–µ–º —ç—Ç–∏ –∑–Ω–∞—á–µ–Ω–∏—è –≤ –∫–∞—á–µ—Å—Ç–≤–µ —Å–≤–æ–π—Å—Ç–≤ –∏ –∏—Ö –∑–Ω–∞—á–µ–Ω–∏–π
    const { name, value } = event.currentTarget;

    if (name === 'name') setName(value);

    if (name === 'number') setNumber(value);
  };

  const handleSubmitForm = event => {
    event.preventDefault();

    // –ó–∞–±–∏—Ä–∞–µ–º –¥–∞–Ω–Ω—ã–µ —Å –∫–æ–Ω—Ç–∞–∫—Ç–∞–º–∏ —Å –≥–ª–æ–±–∞–ª—å–Ω–æ–≥–æ —Å—Ç—ç–π—Ç–∞, –∏ –µ—Å–ª–∏ –∏–º—è —É–∂–µ –±—ã–ª–æ –≤–≤–µ–¥–µ–Ω–æ,
    // –∫–∞–∫–æ–µ –º—ã —Å–Ω–æ–≤–∞ –≤–≤–æ–¥–∏–º –≤ –∏–Ω–ø—É—Ç–µ, —Ç–æ –≤—ã–≤–µ–¥–µ—Ç—Å—è —Å–æ–æ–±—â–µ–Ω–∏–µ –∏ –≤—ã—Ö–æ–¥–∏–º

    const isExist = contacts.find(
      contact => contact.name.toLowerCase() === name.toLowerCase()
    );

    // –µ—Å–ª–∏ –≤–≤–æ–¥–∏–º –∏–º—è –∫–æ–Ω—Ç–∞–∫—Ç–∞, –∫–∞–∫–æ–µ —É–∂–µ –µ—Å—Ç—å –≤ —Ç–µ–ª–µ—Ñ.–∫–Ω–∏–≥–µ, –≤—ã–≤–æ–¥–∏–º —Å–æ–æ–±—â–µ–Ω–∏–µ, —á—Ç–æ –∏–º—è —Ç–∞–∫–æ–µ –µ—Å—Ç—å —Ç–∞–∫–æ–µ –∏ –≤—ã—Ö–æ–¥–∏–º
    if (isExist) {
      toast(
        t => (
          <span
            style={{
              color: '#1a01d4',
            }}
          >
            {name} is already in contacts.
            <button
              style={{
                marginLeft: '15px',
                border: '1px solid rgb(205, 167, 244)',
                borderRadius: '50px',
                background: 'rgb(173, 236, 236)',
                color: '#1a01d4',
                padding: '7px',
              }}
              onClick={() => toast.dismiss(t.id)}
            >
              Close
            </button>
          </span>
        ),
        {
          duration: '1000',
          icon: 'üòï',
          position: 'top-right',
          style: {
            border: '5px solid #e5ccfd',
            borderRadius: '50px',
            background: '#ebffeb',
            color: '#4f0381',
          },
        }
      );
      reset();
      return;
    } else {
      toast(`new contact ${name} added`, {
        position: 'top-right',
        duration: '300',
        icon: 'üëè',
        style: {
          border: '5px solid #e5ccfd',
          borderRadius: '50px',
          background: '#ebffeb',
          color: '#1a01d4',
        },
      });
    }

    //   –∏–Ω–∞—á–µ –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —ç–∫—à–µ–Ω —Å –¥–∞–Ω–Ω—ã–º–∏ –≤ –≥–ª–æ–±–∞–ª—å–Ω—ã–π —Å—Ç—ç–π—Ç –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤

    dispatch(addContact({ name, number, id: nanoid() }));

    reset();
  };

  const reset = () => {
    setName('');
    setNumber('');
  };

  return (
    <form className={css.form_contacts} onSubmit={handleSubmitForm}>
      <Toaster />
      <label className={css.label_contacts} htmlFor={nameId}>
        Name
        <input
          className={css.input_contacts}
          type="text"
          name="name"
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
          value={name}
          required
          onChange={handleChange}
          id={nameId}
        />
      </label>
      <label className={css.label_contacts} htmlFor={numberId}>
        Number
        <input
          className={css.input_contacts}
          type="tel"
          name="number"
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
          required
          value={number}
          onChange={handleChange}
          id={numberId}
        />
      </label>
      <button type="submit" className={css.btn_contacts}>
        Add contact
      </button>
    </form>
  );
};
